import "./Task.scss";

import rocketCoin from "../../../img/svgicons/turbo.svg";
import coin from "../../../img/svgicons/coin-money.svg";
import star from "../../../img/svgicons/star.svg";
import importAll from "../../../commonFn/import-img";

import sponsorLogo from "../../../img/gameSport/logo.webp";
import sponsorBg from "../../../img/gameSport/bg.webp";
import cancel from "../../../img/svgicons/cancel.svg";

import { userApi } from "../../../api/api";
import { useSelector } from "react-redux";
import { useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { useState } from "react";

import PopupLayout from "../PopupLayout/PopupLayout";
import createVideoAd from "../../../commonFn/createVideoAd";

export default function Task({ title, subTitle, tasks, type = "tasks" }) {
  const navigate = useNavigate();

  const accessToken = useSelector((state) => state.userStats.tokens.access);

  const taskImages = importAll(
    require.context("../../../img/taskImages/", false, /\.(png)$/)
  );
  const taskPopupsImages = importAll(
    require.context("../../../img/taskPopups/", false, /\.(webp)$/)
  );
  const zaimImages = importAll(
    require.context("../../../img/zaim/", false, /\.(png)$/)
  );

  const iconMap = {
    eighteen_plus: taskImages[0],
    "1win": taskImages[1],
    "7kcasino": taskImages[2],
    adult_heart: taskImages[3],
    aviasales: taskImages[4],
    bankruptcy_course: taskImages[5],
    bc_game: taskImages[6],
    bitstarz: taskImages[7],
    car: taskImages[8],
    casino: taskImages[9],
    chat_bubble: taskImages[10],
    eyelashes: taskImages[11],
    gamesport: taskImages[12],
    broken_heart: taskImages[13],
    racket: taskImages[14],
    matrix_fate: taskImages[15],
    pickaxe_bitcoin: taskImages[16],
    news: taskImages[17],
    paying_jobs: taskImages[18],
    smotrishka: taskImages[19],
    unity: taskImages[20],
    scales: taskImages[21],
    clicker: taskImages[22],
    friend: taskImages[23],
    wheel: taskImages[24],
    zaim: taskImages[25],
  };
  const iconMapPopups = {
    eighteen_plus_pic: taskPopupsImages[0],
    "1win_pic": taskPopupsImages[1],
    "7kcasino_pic": taskPopupsImages[2],
    adult_heart_pic: taskPopupsImages[3],
    aviasales_pic: taskPopupsImages[4],
    bankruptcy_course_pic: taskPopupsImages[5],
    bc_game_pic: taskPopupsImages[6],
    bitstarz_pic: taskPopupsImages[7],
    car_pic: taskPopupsImages[8],
    casino_pic: taskPopupsImages[9],
    chat_bubble_pic: taskPopupsImages[10],
    eyelashes_pic: taskPopupsImages[11],
    friends_pic: taskPopupsImages[12],
    gamesport_pic: taskPopupsImages[13],
    broken_heart_pic: taskPopupsImages[14],
    racket_pic: taskPopupsImages[15],
    matrix_fate_pic: taskPopupsImages[16],
    pickaxe_bitcoin_pic: taskPopupsImages[17],
    news_pic: taskPopupsImages[18],
    paying_jobs_pic: taskPopupsImages[19],
    smotrishka_pic: taskPopupsImages[20],
    unity_pic: taskPopupsImages[21],
    scales_pic: taskPopupsImages[22],
    zaim_pic: taskPopupsImages[23],
  };
  const iconMapZaim = {
    bystrodengi: zaimImages[0],
    credit_plus: zaimImages[1],
    greenmoney_vip: zaimImages[2],
    joymoney: zaimImages[3],
    turbozaim: zaimImages[4],
    untill_payday: zaimImages[5],
    urgent_money: zaimImages[6],
    vivus_ru: zaimImages[7],
    webbankir: zaimImages[8],
    webzaim: zaimImages[9],
    zaimer: zaimImages[10],
  };

  const handleTaskClick = async (
    e,
    is_ad,
    isDone,
    accessToken,
    task_id,
    progress,
    localNavigate
  ) => {
    if (is_ad === "clicker") {
      e.preventDefault();
      window
        .show_8768118("pop")
        .then(() => {
          navigate(localNavigate.link, { state: { adWatched: true } });
        })
        .catch((e) => {
          alert("Ошибка просмотра рекламы!", e);
        });

      return;
    }
    if (is_ad === "wheel") {
      e.preventDefault();
      window
        .show_8768118()
        .then(() => {
          navigate(localNavigate.link, { state: { adWatched: true } });
        })
        .catch((e) => {
          alert("Ошибка просмотра рекламы!", e);
        });

      return;
    }

    if (isDone) {
      e.preventDefault();
      return;
    }

    if (localNavigate?.link) {
      navigate(localNavigate.link, { state: { adWatched: true } });
      return;
    }

    try {
      const response = await userApi.setTaskDone({
        accessToken,
        task_id,
        available_runs: progress.available_runs,
        completed_runs: progress.completed_runs + 1,
      });
      if (response.status !== "success") {
        console.error("Ошибка получения токенов:", response.message);
      }
    } catch (error) {
      console.log(error);
    }
  };

  function CreateTask({ task }) {
    const {
      task_id,
      icon,
      text,
      link,
      award_text,
      is_ad,
      is_new,
      progress,
      color = "blue",
      popup,
      localNavigate,
    } = task;

    const img = iconMap[icon] || star;
    const isDone = progress.is_done ? "task__done" : "";
    const [isPopupOpen, setPopupOpen] = useState(false);

    if (popup) {
      const { description, button_text, picture } = popup[0];

      const imgPopup = iconMapPopups[picture] || star;
      const handleCloseClick = () => setPopupOpen(false);
      return (
        <>
          <div
            key={task_id}
            onClick={() => {
              setPopupOpen(true);
            }}
            className={`task__task ${color} ${isDone}`}
          >
            <img src={img} alt="" />
            <main className="task__text">
              <h3>{text}</h3>
              <p>{award_text}</p>
              {progress.available_runs && (
                <p>
                  Прогресс {progress.completed_runs} / {progress.available_runs}
                </p>
              )}
            </main>
            {is_ad && <div className="task__ad">AD</div>}
            {!progress.is_done && is_new && <div className="task__new"></div>}
          </div>
          {isPopupOpen && (
            <PopupLayout
              isPopupOpen={isPopupOpen}
              handleCloseClick={handleCloseClick}
              key={`popup` + task_id}
            >
              <h2 className="task-popup__title">{text}</h2>
              <img className="task-popup__bg" src={imgPopup} alt="imgPopup" />
              <p>{description}</p>
              <div className="task-popup__award">
                <img src={rocketCoin} alt="rocketCoin" />
                <p>{award_text}</p>
              </div>
              <NavLink
                to={!localNavigate ? link : ""}
                key={task_id}
                target={!localNavigate && "_blank"}
                className={`btn btn-gradient ${color} ${isDone}`}
                onClick={(e) =>
                  handleTaskClick(
                    e,
                    is_ad,
                    isDone,
                    accessToken,
                    task_id,
                    progress,
                    localNavigate
                  )
                }
              >
                {button_text}
              </NavLink>
            </PopupLayout>
          )}
        </>
      );
    }

    return (
      <NavLink
        to={link || "#"}
        target={localNavigate ? "" : "_blank"}
        key={task_id}
        className={`task__task ${color} ${isDone}`}
        onClick={(e) =>
          handleTaskClick(
            e,
            is_ad,
            isDone,
            accessToken,
            task_id,
            progress,
            localNavigate
          )
        }
      >
        <img src={img} alt="" />
        <main className="task__text">
          <h3>{text}</h3>
          <p>{award_text}</p>
          {progress.available_runs && (
            <p>
              Прогресс {progress.completed_runs} / {progress.available_runs}
            </p>
          )}
        </main>
        {is_ad && <div className="task__ad">AD</div>}
        {!progress.is_done && is_new && <div className="task__new"></div>}
      </NavLink>
    );
  }

  function CreatePartner({ partner }) {
    const {
      task_id,
      icon,
      link,
      text,
      color = "purple",
      award_text,
      progress,
    } = partner;

    const img = iconMapZaim[icon] || star;
    const isDone = progress.is_done ? "task__done" : "";

    return (
      <a
        href={link}
        target="_blank"
        className={`partner__partner ${color} ${isDone}`}
        onClick={(e) =>
          handleTaskClick(e, false, isDone, accessToken, task_id, progress)
        }
        key={task_id}
      >
        <header className="partner__partner-header">
          <aside className="partner__info">
            <div className="partner__img">
              <img src={img} alt="" />
            </div>
            <h3>{text}</h3>
            <h4>Процент одобрения 95%</h4>
          </aside>
          <main className="partner__text">
            <p>
              Сумма кредита <span>от 3000₽ до 30000₽</span>
            </p>
            <p>
              Срок кредита <span>от 7 до 30 дней</span>
            </p>
            <p>
              Возраст <span>от 18 лет</span>
            </p>
            <p>
              Ставка <span>от 0%</span>
            </p>
          </main>
        </header>
        <div className="partner__clicks">
          <img src={rocketCoin} alt="rocketCoin" />
          <p>{award_text}</p>
        </div>
        <div className="partner__coins">
          <img src={coin} alt="coin" />
          <p>+1</p>
        </div>
        {progress.available_runs && (
          <p className="task__progress">
            Прогресс {progress.completed_runs} / {progress.available_runs}
          </p>
        )}
      </a>
    );
  }

  function CreateSponsor({ sponsor }) {
    const {
      task_id,
      icon,
      link,
      text,
      color = "purple",
      award_text,
      progress,
    } = sponsor;

    const isDone = progress.is_done ? "task__done" : "";

    return (
      <div className={`sponsor ${color}`} key={task_id}>
        <h2>GameSport</h2>
        <div className="sponsor-block">
          <div className="sponsor-block__img">
            <img src={sponsorLogo} alt="sponsorLogo" />
          </div>
          <p>GameSport официальный спонсор “Полет в космос”</p>
        </div>
        <div className="sponsor-bg">
          <img src={sponsorBg} alt="sponsorBg" />
        </div>
        <p>
          Получите доступ к аналитике киберспорта на 5 дней всего за 1₽ и
          начните зарабатывать от 150 000₽ в месяц.
        </p>
        <div className="sponsor-sale block-with-bg">5 дней = 1₽</div>
        <p>
          Оформите подписку на PRO-аналитику и получите дополнительные
          возможности от нашего спонсора.
        </p>
        <div className="sponsor-statement">
          {!isDone && <img src={cancel} alt="cancel" />}
          <img src={coin} alt="coin" />
          <p>{award_text}</p>
        </div>
        <a
          href={link}
          target="_blank"
          className={`btn btn-gradient ${isDone}`}
          onClick={(e) =>
            handleTaskClick(e, false, isDone, accessToken, task_id, progress)
          }
        >
          Участвовать
        </a>
        {isDone ? (
          <div className="sponsor-task green">Задание выполнено</div>
        ) : (
          <div className="sponsor-task red">Задание не выполнено</div>
        )}

        {progress.available_runs && (
          <p className="task__progress">
            Прогресс {progress.completed_runs} / {progress.available_runs}
          </p>
        )}
      </div>
    );
  }

  const getComponent = (type, task) => {
    switch (type) {
      case "tasks":
        return <CreateTask key={task.id} task={task} />;
      case "loan":
        return <CreatePartner key={task.id} partner={task} />;
      case "sponsor":
        return <CreateSponsor key={task.id} sponsor={task} />;
      default:
        return null;
    }
  };

  useEffect(() => {
    if (!window.show_8768118) {
      const tag = document.createElement("script");
      tag.src = "//staurdoalse.net/vignette.min.js";
      tag.dataset.zone = "8768118";
      tag.dataset.sdk = "show_8768118";
      document.body.appendChild(tag);
    }
  }, []);

  return (
    <section className={type === "tasks" ? "task" : "partner"}>
      <header className="task__header">
        {title && <h2>{title}</h2>}
        {subTitle && <p>{subTitle}</p>}
      </header>
      {tasks && tasks.map((task) => getComponent(type, task))}
    </section>
  );
}
