import "swiper/css";

import { useState, useRef } from "react";
import { useNavigate } from "react-router-dom";

import { Swiper, SwiperSlide } from "swiper/react";
import importAll from "../../../commonFn/import-img";

export default function Education(params) {
  const images = importAll(
    require.context("../../../img/slider-education/", false, /\.(webp)$/)
  );
  const navigate = useNavigate();
  const swiperRef = useRef(null);
  const [isLastSlide, setIsLastSlide] = useState(false);

  const slides = [
    <>
      <h2>Для чего нужно тапать и как это работает?</h2>
      <img src={images[0]} alt="" />
      <p>
        Кликай по ракете, чтобы зарабатывать очки!
        <br />
        Чем больше тапов — тем выше твой прогресс.
        <br />
        Участвуй в соревнованиях, зарабатывай награды и продвигайся вверх в
        рейтинге.
      </p>
    </>,
    <>
      <h2>Реферальная программа</h2>
      <img src={images[1]} alt="" />
      <p>
        Приглашай друзей и получай бонусы! Каждый приглашённый друг увеличивает
        твои награды.
        <br />
        При достижении определённых уровней реферальной программы ты становишься
        участником розыгрыша различных призов.
        <br />
        Чем больше активных друзей — тем больше бонусов ты получаешь!
      </p>
    </>,
    <>
      <h2>Наш токен и его листинг</h2>
      <img src={images[2]} alt="" />
      <p>
        Все заработанные очки конвертируются в наш токен!
        <br />
        Когда мы достигнем 300 000 пользователей, он выйдет на криптовалютную
        биржу, и ты сможешь использовать его в экосистеме проекта.
      </p>
    </>,
    <>
      <h2>Внутриигровые задания</h2>
      <img src={images[3]} alt="" />
      <p>
        Выполняй задания, чтобы получать дополнительные тапы!
        <br />
        Участвуй в челленджах, выполняй ежедневные миссии и ускоряй свой рост.
        <br />
        Чем активнее ты играешь — тем больше наград получаешь!
      </p>
    </>,
  ];
  const handleButtonClick = () => {
    if (swiperRef.current && swiperRef.current.swiper) {
      const swiper = swiperRef.current.swiper;
      if (swiper.isEnd) {
        navigate("/");
      } else {
        swiper.slideNext();
      }
      setIsLastSlide(swiper.isEnd);
    }
  };
  return (
    <>
      <Swiper ref={swiperRef}>
        {slides.map((slide, index) => {
          return <SwiperSlide key={index}>{slide}</SwiperSlide>;
        })}
      </Swiper>
      <button className="btn btn-gradient" onClick={handleButtonClick}>
        {isLastSlide ? "Играть" : "Далее"}
      </button>
    </>
  );
}
