import "./Boost.scss";

import Tabs from "../Common/Tabs/Tabs";
import Task from "../Common/Task/Task";
import TextHeader from "../Common/TextHeader/TextHeader";

import tipImg from "../../img/svgicons/tip.svg";
import { useSelector } from "react-redux";

export default function Boost() {
  const tasks = useSelector((state) => state.tasks);
  const refferralLink = useSelector(
    (state) => state.userStats.user.refferralLink
  );

  const tabMoreClick = [
    <Task
      title="Авто-кликер"
      tasks={[
        {
          task_id: 9999,
          icon: "star",
          text: "Авто-кликер",
          award_text: "Пусть вкалывает робот, а не человек",
          is_ad: "clicker",
          localNavigate: { flag: true, link: "/" },
          icon: "clicker",
          progress: {
            is_done: false,
          },
        },
      ]}
    />,
    <Task
      title="Пригласи друга"
      tasks={[
        {
          task_id: 9999,
          icon: "star",
          text: "Пригласи друга и начни зарабатывать с его дохода",
          award_text: "+30К кликов",
          is_ad: false,
          progress: {
            is_done: false,
          },
          icon: "friend",
          link: `https://t.me/share/url?url=${encodeURIComponent(
            refferralLink
          )}&text=${encodeURIComponent(
            `\n🚀 ПОЛЕТ В КОСМОС – 🌌 уникальная телеграм-игра, где ты зарабатываешь криптовалюту TON! 💎\n✨ Кликай по КОСМОЛЕТУ 🛸, увеличивай его скорость 🏎, получай 🎁 призы за выполнение заданий и открывай 🌠 новые уровни!\n🎮 ИГРАЙ и отправляйся на космическую охоту за TON! 🚀💫`
          )}`,
          popup: [
            {
              description: `Приглашай друзей , поднимай уровень розыгрыша
                10 друзей - Ozon карта на сумму от 3000 до 5000р 
                30 друзей - Розыгрыш TON от 0.1 до 10
                45 друзей - Розыгрыш iPhone 16 Pro Max 
                `,
              button_text: "Пригласить друга",
              picture: "friends_pic",
            },
          ],
        },
      ]}
    />,

    <Task
      title="Возьми кредит"
      tasks={[
        {
          task_id: 9999,
          icon: "star",
          text: "1 млн кликов за выгодный займ!",
          award_text: "+1 млн кликов",
          is_ad: false,
          localNavigate: { flag: true, tabIndex: 1, link: "/super-click/1" },
          icon: "zaim",
          progress: {
            is_done: false,
          },
          popup: [
            {
              description:
                "Получите до 100 тысяч рублей за 5 минут. Выплаты сразу же на любую карту.",
              button_text: "Получить займ",
              picture: "zaim_pic",
            },
          ],
        },
      ]}
    />,
    <Task
      title="Колесо удачи"
      tasks={[
        {
          task_id: 9999,
          icon: "star",
          text: "Колесо удачи",
          award_text: "Беспроигрышное колесо удачи",
          localNavigate: { flag: true, link: "/wallet" },
          icon: "wheel",
          is_ad: "wheel",
          progress: {
            is_done: false,
          },
        },
      ]}
    />,
    <Task tasks={tasks.moreClicks} />,
  ];
  const tabPartnersTgGames = [<Task tasks={tasks.taskTgPartners} />];
  const tabGoogleTask = [
    <div className="block-with-bg">
      <Task title="Простые ежедневные задания" />
      <div className="count-block">
        <p>100 000</p>
        <p>кликов</p>
      </div>

      <p>
        Перейди в свой браузер, напиши в поиске ключевое слово, указанное в
        задании, и напиши код на сайте.
      </p>

      <div className="tip">
        <img src={tipImg} alt="tipImg" />
        <p>
          <b>Код имеет только цифры!</b> Если код не подходит, то поставьте
          правильные дату и время в вашем телефоне.
        </p>
      </div>
    </div>,
    <Task
      tasks={[
        {
          taskType: "google",
          taskTitle: "Выполни ежедневные задания через поиск Google",
          taskText: "+10 000 000 кликов",
          isNew: true,
        },
        {
          taskType: "google",
          taskTitle: "Найдите код на сайте через поисковик Google",
          taskText: "+100 000 кликов",
        },
        {
          taskType: "star",
          taskTitle: "Играй в Candy Defense - строй $DEFENSE и спасай",
          taskText: "+30 000 кликов",
        },
      ]}
    />,
  ];
  const tabsLinks = [
    "Получить больше кликов",
    "Задания от партнеров ТГ игр",
    // "Google задания",
    // "Задания от подписчиков",
  ];
  const tabsContent = [
    tabMoreClick,
    tabPartnersTgGames,
    // tabGoogleTask,
    // "Задания от подписчиков",
  ];
  return (
    <section className="block-white">
      <TextHeader
        title={[
          <>
            <span>ПОЛЕТ В КОСМОС</span> - первая телеграм игра, которая платит
            TON.
          </>,
        ]}
        subTitle={"Разгони ракету и получи свои TONы сразу же на кошелек!"}
      />
      <Tabs tabsLinks={tabsLinks} tabsContent={tabsContent} />
    </section>
  );
}
