import "./Tabs.scss";
import { useState } from "react";

export default function Tabs({
  tabsLinks,
  tabsContent,
  tabsColor = "pink",
  propsActiveTab = 0,
}) {
  const [activeTab, setActiveTab] = useState(propsActiveTab);
  return (
    <section className="tabs">
      <nav className={`tabs__links ${tabsColor}`}>
        {tabsLinks.map((tab, index) => (
          <button
            key={index}
            className={activeTab === index ? "active" : ""}
            onClick={() => setActiveTab(index)}
          >
            <p>{tab}</p>
          </button>
        ))}
      </nav>
      <div className="tab-content">{tabsContent[activeTab]}</div>
    </section>
  );
}
