import { createSlice } from "@reduxjs/toolkit";
const initialState = {
  task: [],
};

export const tasksSlice = createSlice({
  name: "tasks",
  initialState,
  reducers: {
    setTasks: (state, action) => {
      const newStore = { ...state };

      action.payload.forEach((task) => {
        task.sections.forEach((sectionName) => {
          // Check if the section exists, create it if not
          if (!newStore[sectionName]) {
            newStore[sectionName] = [];
          }

          // Check if the task already exists in the section using task_id
          const taskExists = newStore[sectionName].some(
            (existingTask) => existingTask.task_id === task.task_id
          );

          // Add the task only if it doesn't already exist
          if (!taskExists && task.award_value) {
            newStore[sectionName] = [...newStore[sectionName], task];
          }
        });
      });
      return newStore;
    },
  },
});

export const { setTasks } = tasksSlice.actions;

export default tasksSlice.reducer;
